import React from "react"
import { Card, CardBody, Row, Col, Input, Button, } from "reactstrap"
import { AgGridReact } from "ag-grid-react"
import { ContextLayout } from "../../utility/context/Layout"
// import { ChevronDown } from "react-feather"
import axios from "axios";
import {connect} from "react-redux";
import Customize from "./CustomizeDeals"
import Chart from "react-apexcharts"

import Breadcrumbs from "../../components/@vuexy/breadCrumbs/BreadCrumb"

import "../../assets/scss/plugins/tables/_agGridStyleOverride.scss"
import "../../assets/scss/plugins/tables/_customAgGridStyleOverride.scss"
import "../../assets/scss/pages/app-email.scss"

class TimeToFund extends React.Component {
    state = {
        rowData: null,
        pinnedTopRowData: [],
        pinnedBottomRowData: [],
        paginationPageSize: 20,
        currenPageSize: "",
        getPageSize: "",
        defaultColDef: {},
        columnDefs: [
            {
                headerName: "Finance Person",
                field: "finance_person",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Sales",
                field: "sales",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Approved",
                field: "approved",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Approved(%)",
                field: "approved_percent",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Warranty Total & Pen%",
                field: "warranty_total_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Warranty Gross",
                field: "warranty_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Gap Total & Pen%",
                field: "gap_total_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Gap Gross",
                field: "gap_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Miscfinance Total & Pen%",
                field: "miscfinance_total_pen",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Miscfinance Gross",
                field: "miscfinance_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Reserve",
                field: "reserve",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Back End",
                field: "back_end",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
        ],
        composeMailStatus: false,
        options: {
            colors: this.props.themeColors,
            plotOptions: {
                bar: {
                    horizontal: true
                }
            },
            dataLabels: {
                enabled: false
            },
            xaxis: {
                categories: [
                    "David G",
                    "Zach Elliott",
                    "Jimi Perez",
                    "Garrett N",
                    "Fernando",
                    "Ashlie",
                ],
                tickAmount: 5
            }
        },
        series: [
            {
              data: [7.8, 5.2, 7.8, 20.7, 12.9, 2.6]
            }
        ],
        reportsName: "Company Gross",
        pageName: "company_gross",
    }

  componentDidMount() {
    axios.get("/api/aggrid/data").then(response => {
      // let rowData = response.data.data // replace for product
        let tempdata = {
            finance_person: "08/01/2020",
            sales: "03/12/2020",
            approved: "Segar",
            approved_percent: "23050B",
            warranty_total_pen: "Dodge",
            warranty_gross: "Sprinter",
            gap_total_pen: "Scott Sefrit",
            gap_gross: "$2,333.00",
            miscfinance_total_pen: "$688.00",
            miscfinance_gross: "$3878.00",
            reserve: "13",
            back_end: "13"
        };

        let colWidth = window.innerWidth;
        let columnDefs = this.state.columnDefs
        for (let i = 0; i < this.state.columnDefs.length; i++) {
            columnDefs[i].width = (colWidth-(colWidth/5)) / this.state.columnDefs.length;
            columnDefs[i].minWidth = 150;
        }

    let rowData = [];
    for (let i = 0; i < 20; i++) {
        rowData.push(Object.assign({}, tempdata));
    }

    JSON.stringify(rowData)
    this.setState({
        rowData,
        columnDefs
    });
    })
  }

  UNSAFE_componentWillMount () {
    this.initTable(this.props);
  }

  UNSAFE_componentWillReceiveProps (nextprops) {
    this.initTable(nextprops);
  }

  initTable(props) {
    let pinnedBottomRowData = [];
    pinnedBottomRowData.push({
        date_sold: "Total",
        stock: "2305",
        front_end: "$5,416,843.37",
        back_end: "$2,454,697.21",
        total_gross: "$7,871,540.94",
        average_time: "13"
    });
    pinnedBottomRowData.push({
        date_sold: "Average",
        total_gross: "$2,465.00",
    });

    this.setState({
        pinnedBottomRowData,
        composeMailStatus: false
    });
  }

  onGridReady = params => {
    this.gridApi = params.api
    this.gridColumnApi = params.columnApi
    this.setState({
      currenPageSize: this.gridApi.paginationGetCurrentPage() + 1,
      getPageSize: this.gridApi.paginationGetPageSize(),
      totalPages: this.gridApi.paginationGetTotalPages()
    })
  }

  updateSearchQuery = val => {
    this.gridApi.setQuickFilter(val)
  }

  filterSize = val => {
    if (this.gridApi) {
      this.gridApi.paginationSetPageSize(Number(val))
      this.setState({
        currenPageSize: val,
        getPageSize: val
      })
    }
  }
  handleCustomize() {
    this.handleComposeSidebar("open")
    this.onSetSidebarOpen(false)
  }
  handleComposeSidebar = status => {
    if (status === "open") {
      this.setState({
        composeMailStatus: true
      })
    } else {
      this.setState({
        composeMailStatus: false
      })
    }
  }

  onSetSidebarOpen = open => {
    this.setState({ sidebarOpen: open })
  }
  handleMainAndComposeSidebar = () => {
    this.handleComposeSidebar("close")
    this.onSetSidebarOpen(false)
  }

  render() {
    const { rowData, defaultColDef, pinnedBottomRowData } = this.state

    return (
    <React.Fragment>
        <Breadcrumbs breadCrumbTitle="Finance Penetration" breadCrumbParent="Reports" breadCrumbActive="Finance Penetration"/>
        <Card onClick={this.handleMainAndComposeSidebar}>
            <CardBody>
                <div className="d-flex flex-wrap justify-content-end align-items-center">
                    <div className="mb-1"></div>
                    <div className="d-flex flex-wrap justify-content-between mb-1">
                        <div className="table-input mr-1">
                            <Input placeholder="search..." onChange={e => this.updateSearchQuery(e.target.value)} value={this.state.value}/>
                        </div>
                        <div className="customize-btn">
                            <Button.Ripple block color="primary" onClick={() => this.handleCustomize()}>
                                Customize
                            </Button.Ripple>
                        </div>
                        <div className="export-btn">
                            <Button.Ripple color="primary" onClick={() => this.gridApi.exportDataAsCsv()}>
                                Export as CSV
                            </Button.Ripple>
                        </div>
                    </div>
                </div>
                <Row>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Deals:</Col>
                            <Col sm="6" xs="6">7274</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Back End Gross:</Col>
                            <Col sm="6" xs="6">$7,871,540.94</Col>
                        </Row>
                    </Col>
                    <Col sm="6"></Col>
                </Row><br />
                <Row>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Unfunded Deals:</Col>
                            <Col sm="6" xs="6">226</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Unfunded Amount:</Col>
                            <Col sm="6" xs="6">$870,743.00</Col>
                        </Row>
                    </Col>
                    <Col sm="6"></Col>
                </Row><br />
                <Row>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Warranties Sold:</Col>
                            <Col sm="6" xs="6">4181</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Warranty Gross:</Col>
                            <Col sm="6" xs="6">$870,743.00</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Warranty Penetration:</Col>
                            <Col sm="6" xs="6">4181 57.5%</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Warranty Average:</Col>
                            <Col sm="6" xs="6">$764.24</Col>
                        </Row>
                    </Col>
                </Row><br />
                <Row>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Gap Plans Sold:</Col>
                            <Col sm="6" xs="6">4462</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Gap Gross:</Col>
                            <Col sm="6" xs="6">$870,743.00</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Gap Penetration:</Col>
                            <Col sm="6" xs="6">4462 78%</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">Gap Average:</Col>
                            <Col sm="6" xs="6">$405.16</Col>
                        </Row>
                    </Col>
                </Row><br />
                <Row>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">MiscFin Sold:</Col>
                            <Col sm="6" xs="6">5030</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">MiscFin Gross:</Col>
                            <Col sm="6" xs="6">$870,743.00</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">MiscFin Penetration:</Col>
                            <Col sm="6" xs="6">5030 69.2%</Col>
                        </Row>
                    </Col>
                    <Col sm="3">
                        <Row>
                            <Col sm="6" xs="6">MiscFin Average:</Col>
                            <Col sm="6" xs="6">$197.93</Col>
                        </Row>
                    </Col>
                </Row>
                <Chart options={this.state.options} series={this.state.series} type="bar" height={350} />
                <div className="d-flex flex-wrap mb-1">
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Total Deals
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Total Front End
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Average Back End
                        </Button.Ripple>
                    </div>
                </div>
            </CardBody>
        </Card>
        <div className="email-application">
            <Customize handleComposeSidebar={this.handleComposeSidebar} currentStatus={this.state.composeMailStatus}/>
            <Card className="agGrid-card" style={{height: "100% !important", paddingBottom: "2%"}} onClick={this.handleMainAndComposeSidebar}>
                <CardBody className="py-0" style={{height: "100% !important"}}>
                {
                this.state.rowData === null ? null : (
                    <div className="ag-theme-material w-100 my-2 ag-grid-table" style={{height: "100% !important",width: "100% !important"}}>                        
                        <ContextLayout.Consumer>
                            {context => (
                            <AgGridReact
                                gridOptions={{}}
                                rowSelection="multiple"
                                defaultColDef={defaultColDef}
                                columnDefs={this.state.columnDefs}
                                rowData={rowData}
                                onGridReady={this.onGridReady}
                                floatingFilter={true}
                                pagination={true}
                                enableRtl={context.state.direction === "rtl"}
                                
                                pinnedBottomRowData={pinnedBottomRowData}
                                rowHeight="40"
                            />
                            )}
                        </ContextLayout.Consumer>
                    </div>
                )}
                </CardBody>
            </Card>
        </div>
    </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
    return {
        app: state.calender
    }
}

export default connect(mapStateToProps, {})(TimeToFund)
